@import url(https://fonts.googleapis.com/css2?family=Mountains+of+Christmas&display=swap);
html{display:flex}body{max-width:450px;margin:auto;font-family:Arial, Helvetica, sans-serif}h1{font-family:"Mountains of Christmas", cursive;font-size:4rem;text-align:center}p{font-size:1.5rem;margin:1rem;text-align:center}.App{display:flex;flex-flow:column;color:white}.App>div{display:flex;flex-flow:column}.App .show{-webkit-animation:1s fadein;animation:1s fadein}.App .no-show{display:none}.App button{background:#6e1313;padding:0.5rem 1.5rem;border:none;box-shadow:0px 2px 4px 4px rgba(0,0,0,0.25);margin:auto;margin-top:1.5rem;color:white;font-size:2rem}.App img{margin-top:2rem;border:5px solid white;box-shadow:0px 2px 4px 4px rgba(0,0,0,0.25)}.App h1{margin:auto}.App .christmas{margin-top:5rem}@-webkit-keyframes fadein{from{opacity:0}to{opacity:1}}@keyframes fadein{from{opacity:0}to{opacity:1}}

html,body{height:100%;background-color:#b33}.christmas{position:absolute;top:50%;left:50%;margin-top:-100px;margin-left:-100px;width:200px;height:200px;position:relative;border-radius:50%;background-color:#0c1e42;background-image:radial-gradient(circle at 50% 18%, yellow 3%, transparent 3.5%),linear-gradient(60deg, #7dae41 8%, transparent 8.5%),linear-gradient(-60deg, #79a83f 8%, transparent 8.5%),linear-gradient(60deg, #75a33d 10%, transparent 10.5%),linear-gradient(-60deg, #719d3b 10%, transparent 10.5%),linear-gradient(60deg, #6d9839 12%, transparent 12.5%),linear-gradient(-60deg, #699237 12%, transparent 12.5%),linear-gradient(80deg, #855726 5%, transparent 5.5%),linear-gradient(-80deg, #7f5324 5%, transparent 5.5%);background-position:0 0,100px 80px,100px 80px,100px 111.11111px,100px 111.11111px,100px 142.85714px,100px 142.85714px,100px 160px,100px 160px}.christmas:before{content:"";position:absolute;top:0;left:0;width:100%;height:100%;border-radius:50%;transform:rotate(-15deg);background-image:radial-gradient(circle at 30% 30%, #fff 1.5%, transparent 2.5%),radial-gradient(circle at 80% 70%, #fff 1.5%, transparent 2.5%),radial-gradient(circle at 30% 50%, #fff 1%, transparent 2%),radial-gradient(circle at 70% 85%, #fff 1%, transparent 2%);background-size:100% 100%, 100% 100%, 50% 100%, 50% 50%;background-position:0 0, 0 0, 0 0, 0 0;-webkit-animation:snow 6s linear infinite forwards;animation:snow 6s linear infinite forwards}@-webkit-keyframes snow{100%{background-position:0px 200px,0 200px,0px 200px,0px 200px}}@keyframes snow{100%{background-position:0px 200px,0 200px,0px 200px,0px 200px}}

